import React from 'react';
import logo from '../../logo.png';
import './Cuerpo.css';
import { Container, Navbar, Nav} from 'react-bootstrap';
import OwnedMaps from '../OwnedMaps/OwnedMaps';
import SelectedMaps from '../SelectedMaps/SelectedMaps';
import AllMaps from '../AllMaps/AllMaps';
import {BrowserRouter, Navigate, Routes, Route, NavLink} from "react-router-dom";
import { connect } from 'react-redux';

const mapStateToProps = store => ({
  ownedMaps: store.mapasOwnedMaps.ownedMaps,
  selectedMaps: store.mapasSelectedMaps.selectedMaps
})

class Cuerpo extends React.Component {
  constructor (props) {
    super (props);
    this.state ={}; 
  }
  
  render (){
    return (
      <BrowserRouter>
      <header>
        <img src={logo} alt='Map Selector Logo' className='logo'></img>
        <Navbar bg="#232323" expand="lg"  variant="dark">
          <Container>
            <Navbar.Toggle aria-controls="basic-navbar-nav" />
            <Navbar.Collapse id="basic-navbar-nav" >
              <Nav className="ms-auto">
                <NavLink className= 'opcion' {...(navData) => navData.isActive ? "active" : "" } to='/all-maps'>All Maps</NavLink>
                <span className='barra'>|</span>
                <NavLink  className= 'opcion' {...(navData) => navData.isActive ? "active" : "" } to='/owned-maps'>Owned Maps ({this.props.ownedMaps.length})</NavLink>
                <span className='barra'>|</span>
                <NavLink  className= 'opcion' {...(navData) => navData.isActive ? "active" : "" } to='/selected-maps'>Selected Maps ({this.props.selectedMaps.length})</NavLink>
              </Nav>
            </Navbar.Collapse>
          </Container>
        </Navbar>
      </header>
      <main>
        <Routes>
          <Route path='/all-maps' element={<AllMaps/>}/>
          <Route path='/owned-maps' element={<OwnedMaps/>}/>
          <Route path="/selected-maps" element={<SelectedMaps/>} />
          <Route path="*" element={<Navigate to="/all-maps" replace />}/>   
        </Routes>
      </main>
      </BrowserRouter>
    );
  }
}
export default connect(mapStateToProps)(Cuerpo);
