import React from "react";
import {Button} from "react-bootstrap";
import "bootstrap/dist/css/bootstrap.min.css";
import { connect } from 'react-redux';
import Mapas from '../../maps/dir';

const mapStateToProps = store => ({
    selectedMaps: store.mapasSelectedMaps.selectedMaps
  })


class SelectedMaps extends React.Component {
    constructor(props){
        super(props);
        this.state = {}
        this.eliminarTodosSelectedMaps = this.eliminarTodosSelectedMaps.bind(this);
    }

    eliminarTodosSelectedMaps () {
        this.props.dispatch({ type: 'ELIMINAR_SELECTEDMAPS', payload: []});
    }
    
    render(){
        return (
            <>
            <h1>Selected Maps</h1>
            {this.props.selectedMaps.length > 0 ? 
            <Button id={this.props.id} className="boton" variant="secondary" onClick={this.eliminarTodosSelectedMaps}>Delete all selected maps</Button>
            : <></>}<br/><br/>
            <Mapas existe={this.props.selectedMaps.length} noExiste={"No maps selected"} arrayId={this.props.selectedMaps}/>
            </>
        )
    }    
} export default connect(mapStateToProps)(SelectedMaps);