import React from 'react';
import './BotonesAllMaps.css';
import {Container, Row, Col, Button } from 'react-bootstrap';
import { connect } from "react-redux";

const mapStateToProps = store => ({
    ownedMaps: store.mapasOwnedMaps.ownedMaps,
    selectedMaps: store.mapasSelectedMaps.selectedMaps
  })

class BotonesAllMaps extends React.Component {
    constructor (props){
        super(props);
        this.state = {
            ownedmapsboolean:false,
            selectedmapsboolean: false
        }

        this.agregarOwnedMaps = this.agregarOwnedMaps.bind(this);
        this.removerOwnedMaps = this.removerOwnedMaps.bind(this);
        this.seleccionarMap = this.seleccionarMap.bind(this);
        this.deseleccionarMap = this.deseleccionarMap.bind(this);
    }

    componentDidMount () {
        {this.props.ownedMaps.includes(this.props.id) ?
            this.setState({ownedmapsboolean: true})
        :
            this.setState({ownedmapsboolean: false})
        };
        {this.props.selectedMaps.includes(this.props.id) ?
            this.setState({selectedmapsboolean: true})
        :
            this.setState({selectedmapsboolean: false})
        };
    }
    agregarOwnedMaps (event) {
        this.setState({ownedmapsboolean : true});
        this.props.dispatch({ type: 'AGREGAR_OWNEDMAPS', payload: this.props.id});
    };

    removerOwnedMaps (event) {
        this.setState({ownedmapsboolean : false});
        this.props.dispatch({ type: 'BORRAR_OWNEDMAPS', payload: this.props.id});
        
    };

    seleccionarMap (event) {
        this.setState({selectedmapsboolean : true});
        this.props.dispatch({ type: 'SELECCIONAR_MAPA', payload: this.props.id});
    };

    deseleccionarMap (event) {
        this.setState({selectedmapsboolean : false});
        this.props.dispatch({ type: 'DESELECCIONAR_MAPA', payload: this.props.id});
        
    };
  
   
    render(){
        

        return(
            <div className='contenedor-botones'>
                <Container>
                    {window.location.pathname === '/all-maps' ?
                    <Row xs={2} sm={1} className="contenedor-botones">
                        <Col className="col-boton">
                            {this.state.ownedmapsboolean ? 
                            <Button id={this.props.id}  className="boton" variant="success" onClick={this.removerOwnedMaps}>Added to owned maps!<span className='sub'>Click again to remove</span></Button>
                            :
                            <Button id={this.props.id}  className="boton" variant="outline-secondary" onClick={this.agregarOwnedMaps}>Add to owned maps</Button>}
                        </Col>
                        <Col className="col-boton">
                            {this.state.selectedmapsboolean ? 
                            <Button id={this.props.id}  className="boton" variant="danger" onClick={this.deseleccionarMap}>Map selected!<span className='sub'>Click again to remove</span></Button>
                            :
                            <Button id={this.props.id} className="boton" variant="outline-secondary" onClick={this.seleccionarMap}>Select map</Button>}
                        </Col>
                    </Row>
                    : <></>} 
                    {window.location.pathname === '/owned-maps' ?
                    <Row xs={2} sm={1} className="contenedor-botones">
                        <Col className="col-boton">
                            <Button id={this.props.id}  className="boton" variant="success" onClick={this.removerOwnedMaps}>Remove from owned maps</Button>
                        </Col>
                        <Col className="col-boton">
                            {this.state.selectedmapsboolean ? 
                            <Button id={this.props.id}  className="boton" variant="danger" onClick={this.deseleccionarMap}>Map selected!<span className='sub'>Click again to remove</span></Button>
                            :
                            <Button id={this.props.id} className="boton" variant="outline-secondary" onClick={this.seleccionarMap}>Select map</Button>}
                        </Col>
                    </Row>
                    : <></>} 
                    {window.location.pathname === '/selected-maps' ?
                    <Row xs={2} sm={1} className="contenedor-botones">
                        <Col className="col-boton">
                            <Button id={this.props.id}  className="boton" variant="danger" onClick={this.deseleccionarMap}>Deselect map</Button>
                        </Col>
                        <Col className="col-boton">
                        {this.state.ownedmapsboolean ? 
                            <Button id={this.props.id}  className="boton" variant="success" onClick={this.removerOwnedMaps}>Added to owned maps!<span className='sub'>Click again to remove</span></Button>
                            :
                            <Button id={this.props.id}  className="boton" variant="outline-secondary" onClick={this.agregarOwnedMaps}>Add to owned maps</Button>}   
                        </Col>
                    </Row>
                    : <></>}
                </Container>
            </div>
        )
    }
}

export default connect(mapStateToProps)(BotonesAllMaps);

