import React from "react";
import "bootstrap/dist/css/bootstrap.min.css";
import { connect } from 'react-redux';
import Mapas from '../../maps/dir';

const mapStateToProps = store => ({
    allMaps: store.mapasAllMaps.allMaps
  })

class AllMaps extends React.Component {
    constructor(props){
        super(props);
        this.state = {}
    }
    
    render(){
        return (
            <>
                <h1>All Maps</h1>
                <Mapas existe={this.props.allMaps.length} arrayId={this.props.allMaps} titulo={"All Maps"}/>
            </>
        )
    }    
} export default connect(mapStateToProps)(AllMaps);



    
