import { createStore, combineReducers } from 'redux';
import mapasAllMaps from './ArrayAllMaps/reducer';
import mapasOwnedMaps from './ArrayOwnedMaps/reducer';
import mapasSelectedMaps from './ArraySelectedMaps/reducer';


const myReducers = combineReducers({
  mapasAllMaps,
  mapasOwnedMaps,
  mapasSelectedMaps
});

export default () => {
  return {
    ...createStore(myReducers,
      window.__REDUX_DEVTOOLS_EXTENSION__ && window.__REDUX_DEVTOOLS_EXTENSION__()
      )
  }
}