import React from "react";
import {Button} from "react-bootstrap";
import "bootstrap/dist/css/bootstrap.min.css";
import { connect } from 'react-redux';
import Mapas from '../../maps/dir';

const mapStateToProps = store => ({
    ownedMaps: store.mapasOwnedMaps.ownedMaps,
  })

class OwnedMaps extends React.Component {
    constructor(props){
        super(props);
        this.state = {}
        this.eliminarTodosOwnedMaps = this.eliminarTodosOwnedMaps.bind(this);
    }

    eliminarTodosOwnedMaps () {
        this.setState({ownedmapsboolean : true});
        this.props.dispatch({ type: 'ELIMINAR_TODOSOWNEDMAPS', payload: []});
    }
    
    render(){
        return (
            <>
            <h1>Owned Maps</h1>
            {this.props.ownedMaps.length > 0 ? 
            <Button id={this.props.id} className="boton" variant="secondary" onClick={this.eliminarTodosOwnedMaps}>Delete all owned maps</Button>
            : <></>}<br/><br/>
            <Mapas existe={this.props.ownedMaps.length} noExiste={"No maps in your collection"} arrayId={this.props.ownedMaps}/>
            </>
        )
    }    
} export default connect(mapStateToProps)(OwnedMaps);