const initialState = {ownedMaps: []};

export default (state = initialState, action) => {
    switch(action.type){
        case 'AGREGAR_OWNEDMAPS' :
            return {
                ...state,
                ownedMaps: [ ...state.ownedMaps, action.payload ]
            };
        case 'BORRAR_OWNEDMAPS' :
                return {
                    ...state,
                    ownedMaps: state.ownedMaps.filter(mapaId => mapaId !== action.payload)
            };
        case 'ELIMINAR_TODOSOWNEDMAPS' :
            return {
                ...state,
                ownedMaps: action.payload
        };
        default:
            return state;
    }
}

export const mapasOwnedMaps = state => state.ownedMaps;