const initialState = {selectedMaps: []};

export default (state = initialState, action) => {
    switch(action.type){
        case 'SELECCIONAR_MAPA' :
            return {
                ...state,
                selectedMaps: [ ...state.selectedMaps, action.payload ]
        };
        case 'DESELECCIONAR_MAPA' :
            return {
                ...state,
                selectedMaps: state.selectedMaps.filter(mapaId => mapaId !== action.payload)
        };
        case 'ELIMINAR_SELECTEDMAPS' :
            return {
            ...state,
            selectedMaps: action.payload
        };
            default:
                return state;
    }
}

export const mapasSelectedMaps = state => state.selectedMaps;